interface Props {
  width: string;
  height: string;
}

export const ArrowDownSVG: React.FC<Props> = ({ width, height }) => {
  return (
    <svg
      id="eGhn0cmvlo81"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 100 100"
      shape-rendering="geometricPrecision"
      text-rendering="geometricPrecision"
    >
      <image
        x={"40%"}
        y={"10%"}
        width={width}
        height={height}
        xlinkHref="data:image/gif;base64,"
        preserveAspectRatio="xMidYMid meet"
      />
    </svg>
  );
};
